
















import Vue from "vue";
import Component from "vue-class-component";
import store from "@/store";
import { PostModel, RequestData } from "@/x-vue/services/interfaces";
import PostViewComponent from "@/x-vue/components/forum/PostView.vue";
import { AppService } from "@/service/app.service";

@Component({
  components: {
    PostViewComponent,
  },
}) // without this, Vue lifecycle methods will not be invoked
export default class PostView extends Vue {
  loading = false;
  app = AppService.instance;

  pageNotFound = false;

  get post(): PostModel {
    return store.state.post;
  }
  async mounted(): Promise<void> {
    console.log("PostView::mounted(); ");
    this.loading = true;

    const arr = location.href.split("/");
    const last = arr[arr.length - 1];
    let options: RequestData = {
      idx: last,
    };

    try {
      console.log(options);
      const res = await this.app.api.postGet(options);
      this.loading = false;
      store.state.post = this.post.fromJson(res);
    } catch (e) {
      this.loading = false;
      this.pageNotFound = true;

      this.app.error(e);
    }
  }
}
